<template>
  <div id="os_pie" style="height:400px;"></div>
</template>

<script>
import echarts from "echarts";
import visitApi from "@/api/visitApi";
export default {
  data() {
    return {};
  },
  mounted() {
    this.initChart();
    let that = this;
    let interval = setInterval(() => {
      this.initChart();
      if (that.$route.path != "/index") {
        clearInterval(interval);
      }
    }, 5000);
  },
  methods: {
    initChart: function() {
      var dom = document.getElementById("os_pie");
      if (!dom) {
        return false;
      }
      var myChart = echarts.init(dom);
      visitApi.countOS().then(res => {
        let data = res.data;
        let nameList = [];
        data.map(o => {
          nameList.push(o.name);
        });
        var option = {
          title: {
            text: "今日访问终端占比",
            left: "left"
          },
          tooltip: {
            trigger: "item",
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            orient: "vertical",
            left: "right",
            data: nameList
          },
          series: [
            {
              name: "访问来源",
              type: "pie",
              radius: "55%",
              center: ["50%", "60%"],
              data: data,
              emphasis: {
                itemStyle: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: "rgba(0, 0, 0, 0.5)"
                }
              }
            }
          ]
        };
        myChart.setOption(option);
        window.addEventListener("resize", function() {
          myChart.resize();
        });
      });
    }
  }
};
</script>

